import styles from "./Users.module.css";
import React from "react";
import {NavLink} from "react-router-dom";
import Paginator from "../common/Paginator";

let Users=(props)=>{
    return(
        <div>
           <Paginator currentPage={props.currentPage} onPageChanged={props.onPageChanged} totalUsersCount={props.totalUsersCount} pageSize={props.pageSize} portionSize={10}/>
            {props.users.map(u=><div key={u.id}>
                    <span>
                        <div><NavLink to={'/profile/'+u.id}><img src={u.photos.small!= null? u.photos.small:'https://sun9-63.userapi.com/impg/c858224/v858224757/1bf82f/dDn3reK2Ytw.jpg?size=320x320&quality=96&sign=68c613560d0983b7967bc3a9324f33f9&c_uniq_tag=QgwS6lTmDJPqFpBYmISaY3qipVqoV31nBKyhe9H5cy8&type=album'} className={styles.photo}/></NavLink></div>
                        <div>
                            {u.followed
                                ? <button disabled={props.followingInProgress.some(id=>id==u.id)}
                                          onClick={()=>{props.unfollow(u.id)}}>Unfollow</button>

                                :<button disabled={props.followingInProgress.some(id=>id==u.id)}
                                         onClick={()=>{props.follow(u.id)}}>Follow</button> }
                        </div>
                    </span>
                <span>
                        <span>
                            <div>{u.name}</div>
                            <div>{u.status}</div>
                        </span>
                        <span>
                            <div>{'u.location.city'}</div>
                            <div>{'u.location.country'}</div>
                        </span>
                    </span>
            </div>)}
        </div>)}
export default Users