import {connect} from "react-redux";
import {
    follow,
    unfollow,
    setCurrentPage, toggleIsFollowing, getUsers
} from "../../redux/usersReducer";
import React from "react";
import Users from "./Users";
import Preloader from "../common/preloader";
import {withAuthRedirect} from "../../hoc/WithAuthRedirect";
import {compose} from "redux";
import {
    getCurrentPage,
    getFollowingInProgress,
    getIsFatching,
    getPageSize,
    getTotalUsersCount, getUsersBySelector, getUserSuperSelector
} from "../../redux/usersSelectors";

class UsersConteiner extends React.Component{
    componentDidMount(){
        this.props.getUsers(this.props.currentPage,this.props.pageSize)
    }

    onPageChanged=(pageNumber)=>{
        this.props.setCurrentPage(pageNumber);
        this.props.getUsers(pageNumber,this.props.pageSize)}

    render(){return<>
        {this.props.isFatching ? <Preloader/> : null}
        <Users
        totalUsersCount={this.props.totalUsersCount}
        pageSize={this.props.pageSize}
        currentPage={this.props.currentPage}
        onPageChanged={this.onPageChanged}
        users={this.props.users}
        unfollow={this.props.unfollow}
        follow={this.props.follow}
        followingInProgress={this.props.followingInProgress}/>
    </>
    }
}
let mapStateToProps=(state)=>{
    return{
        users: getUserSuperSelector(state),
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUsersCount(state),
        currentPage:getCurrentPage(state),
        isFatching:getIsFatching(state),
        followingInProgress:getFollowingInProgress(state)
        }
}
export default compose(connect(mapStateToProps,{
    follow, unfollow, setCurrentPage, toggleIsFollowing,getUsers}),withAuthRedirect)(UsersConteiner)